import React from 'react'
import { useTranslation } from 'react-i18next';
import style from './Contact.module.css'

export default function Contact() {
    const { t } = useTranslation();

    return (
        <div>
            <div className="w-100">
                <iframe src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d26545717.401285414!2d-74.33867379599896!3d35.69068963409167!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x54eab584e432360b%3A0x1c3bb99243deb742!2z2KfZhNmI2YTYp9mK2KfYqiDYp9mE2YXYqtit2K_YqQ!5e0!3m2!1sar!2seg!4v1696403385905!5m2!1sar!2seg" width="100%" height="450" allowfullscreen="" loading="lazy" referrerpolicy="no-referrer-when-downgrade"></iframe>
            </div>

            <div className="my-3 container">
                <div className="row m-auto gy-2">
                    <div className="col-md-8">

                        <h2 className="fw-bolder">
                            {t('contact.contact form')}
                        </h2>

                        <form>
                            <div className="pt-3 row gy-3">
                                <div className="col-md-4">
                                    <label htmlFor="name">{t('contact.name')} <span className={`${style.star}`}>*</span> </label>
                                    <input type="text" id='name' className={`form-control`} />
                                </div>

                                <div className="col-md-4">
                                    <label htmlFor="email">{t('contact.email')} <span className={`${style.star}`}>*</span> </label>
                                    <input type="email" id='email' className={`form-control`} />
                                </div>

                                <div className="col-md-4">
                                    <label htmlFor="subject">{t('contact.subject')} </label>
                                    <input type="text" id='subject' className={`form-control`} />
                                </div>
                                <div className="col-md-12">
                                    <label htmlFor="text">{t('contact.message')} <span className={`${style.star}`}>*</span> </label>
                                    <textarea type="text" id='text' className={`form-control`} ></textarea>
                                </div>
                            </div>
                            <button className='btn mt-3 btn-danger'>{t('contact.send')}</button>
                        </form>

                    </div>
                    <div className="col-md-4">
                        <h2 className="fw-bolder">
                            {t('contact.contact')}
                        </h2>

                        <div className='pt-3'>
                            <i className={`fa-brands fa-twitter ${style.sochial}`}></i> <span>BeTube</span>
                        </div>
                        <div className='pt-3'>
                            <i className={`fa-solid fa-phone ${style.sochial}`}></i> <span>+1700 224-5278</span>
                        </div>
                        <div className='pt-3'>
                            <i className={`fa-solid fa-envelope ${style.sochial}`}></i> <span>info@betube.com</span>
                        </div>
                        <div className='pt-3'>
                            <i className={`fa-solid fa-clock ${style.sochial}`}></i> <span>Monday - Friday 9:00 - 21:00</span>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}
