import React from 'react'
import { Link } from 'react-router-dom'
import { NavLink } from 'react-router-dom'
import style from '../styles/Header.module.css'
import film from '../../../../src/assets/images/film-camera.png';
import { useTranslation } from 'react-i18next';
import Dialog from '@mui/material/Dialog';
import DialogActions from '@mui/material/DialogActions';
import DialogContent from '@mui/material/DialogContent';
import DialogContentText from '@mui/material/DialogContentText';
import DialogTitle from '@mui/material/DialogTitle';
import useMediaQuery from '@mui/material/useMediaQuery';
import { useTheme } from '@mui/material/styles';
import Button from '@mui/material/Button';
export default function Header(props) {
    const [t, i18n] = useTranslation();
    const [open, setOpen] = React.useState(false);
    const theme = useTheme();
    const fullScreen = useMediaQuery(theme.breakpoints.down('md'));

    const handleClickOpen = () => {
        setOpen(true);
    };

    const handleClose = () => {
        setOpen(false);
    };
    function getLangAr() {
        i18n.changeLanguage('ar');
        i18n.language = 'ar'
        localStorage.setItem('i18nextLng', 'ar');
    }

    function getLangEn() {
        i18n.changeLanguage('en');
        i18n.language = 'en'
        localStorage.setItem('i18nextLng', 'en');
    }

    return (
        <>

            <nav className={`${style.navFirst}`}>

                <div className="container-fluid">
                    <div className="d-flex justify-content-between align-items-center">
                        <div>
                            <i className="fa-brands fa-facebook-f socialLinks"></i>
                            <i className="fa-brands fa-twitter socialLinks"></i>
                            <i className="fa-brands fa-youtube socialLinks"></i>
                            <i className="fa-brands fa-google socialLinks"></i>
                            <i className="fa-brands fa-instagram socialLinks"></i>
                            <i className="fa-brands fa-vimeo-v socialLinks"></i>
                        </div>

                        <div>
                            <ul className="navbar-nav mb-lg-0">
                                {props.userData ?
                                    <>
                                        <Button className='text-muted' onClick={handleClickOpen}>
                                            {t('header.logout')}
                                        </Button>
                                        <Dialog
                                            fullScreen={fullScreen}
                                            open={open}
                                            onClose={handleClose}
                                            aria-labelledby="responsive-dialog-title"
                                        >

                                            <DialogContent>
                                                <DialogContentText>
                                                    {t('header.logout message')}
                                                </DialogContentText>
                                            </DialogContent>
                                            <DialogActions>
                                                <Button autoFocus onClick={handleClose}>
                                                    {t('header.not login')}
                                                </Button>
                                                <Button onClick={handleClose} autoFocus>
                                                    <Link onClick={props.Logout} className="nav-link">{t('header.logout')}</Link>
                                                </Button>
                                            </DialogActions>
                                        </Dialog>
                                    </> :
                                    <>
                                        <div className={`px-4 ${style.bgLinks}`}>
                                            <li className="nav-item">
                                                <Link className="nav-link text-white" to="login">{t('header.login')} </Link>
                                            </li>
                                            <li className="nav-item">
                                                <Link className="nav-link text-white"> / </Link>
                                            </li>
                                            <li className="nav-item">
                                                <Link className="nav-link text-white" to="register">{t('header.register')}</Link>
                                            </li>
                                        </div>
                                    </>
                                }
                            </ul>
                        </div>
                    </div>
                </div>
            </nav>

            <nav className={`navbar navbar-expand-lg ${style.navSecond}`}>
                <div className="container-fluid">
                    <Link className="navbar-brand" to="home">
                        <img src={film} className={`${style.logo}`} alt="film" /><span className='text-muted'>BeTube</span>
                    </Link>
                    <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
                        <span className="navbar-toggler-icon"></span>
                    </button>
                    <div className="collapse navbar-collapse" id="navbarSupportedContent">
                        <ul className="navbar-nav me-auto mb-2 mb-lg-0">
                        </ul>

                        <ul className="navbar-nav mb-2 mb-lg-0">
                            <li className={`nav-item order-lg-first order-last ${style.icons}`}>
                                <div className='mt-3'>
                                    <i className={`fa-brands fa-facebook-f ${style.sochial}`}></i>
                                    <i className={`fa-brands fa-twitter ${style.sochial}`}></i>
                                    <i className={`fa-brands fa-youtube ${style.sochial}`}></i>
                                    <i className={`fa-brands fa-google ${style.sochial}`}></i>
                                    <i className={`fa-brands fa-instagram ${style.sochial}`}></i>
                                    <i className={`fa-brands fa-vimeo-v ${style.sochial}`}></i>
                                </div>
                            </li>
                            <li className="nav-item">
                                <NavLink exact activeclassname="active" className={`nav-link ${style.iconHeader}`} to="home"><i className={`fa fa-home px-2`}></i> {t('Home.Home')}</NavLink>
                            </li>
                            <li className="nav-item">
                                <NavLink activeclassname="active" className={`nav-link ${style.iconHeader}`} to="about"><i className={`fa fa-user px-2`}></i>{t('header.about')}</NavLink>
                            </li>
                            <li className="nav-item">
                                <NavLink activeclassname="active" className={`nav-link ${style.iconHeader}`} to="contact"><i className={`fa fa-envelope px-2`}></i>{t('header.contact')}</NavLink>
                            </li>
                            {props.userData ?
                                <>
                                    <li className="nav-item">
                                        <NavLink activeclassname="active" className={`nav-link ${style.iconHeader}`} to="movies"><i className={`fa fa-film px-2`}></i>{t('header.movie')}</NavLink>
                                    </li>
                                </>
                                : ""}
                            {props.userData ?
                                <>
                                    <li className={`${style.icons}`}>
                                        <Link onClick={props.Logout} className="nav-link">Logout</Link>
                                    </li>
                                </> :
                                <div className={`${style.icons}`}>
                                    <li className="nav-item">
                                        <Link className="nav-link" to="login">Login</Link>
                                    </li>
                                    <li className="nav-item">
                                        <Link className="nav-link" to="register">Regiser</Link>
                                    </li>
                                </div>
                            }

                            <li>
                                {
                                    i18n.language == 'en' ?
                                        <button className='btn btn-danger' onClick={() => getLangAr()}>العربية</button> :
                                        <button className='btn btn-danger' onClick={() => getLangEn()}>English</button>
                                }
                            </li>
                        </ul>
                    </div>
                </div>
            </nav>
        </>
    )
}
