import Axios from 'axios';
import React, { useEffect, useState } from 'react';
import { Link } from 'react-router-dom';
import style from './Home.module.css'
import $ from 'jquery';
import HomeHeader from './Home/HomeHeader';
import { useTranslation } from 'react-i18next';
import Rating from '@mui/material/Rating';
import Box from '@mui/material/Box';
import StarIcon from '@mui/icons-material/Star';
import boot from '../../../../assets/images/boots.jpg';
import sale from '../../../../assets/images/sale.jpg';
import boster from '../../../../assets/images/goodleadv.png';

export default function Home() {
  const [trendingMovies, setTrendingMovies] = useState([]);
  const nums = new Array(5).fill(1).map((element, index) => index + 1);
  const { t } = useTranslation();
  async function getTrending(pageNumber) {
    let { data } = await Axios.get(`https://api.themoviedb.org/3/discover/movie?api_key=f1aca93e54807386df3f6972a5c33b50&language=en-US&sort_by=popularity.desc&include_adult=false&include_video=false&page=${pageNumber}`)
    setTrendingMovies(data.results.slice(0, 4));
  }

  useEffect(() => {
    $('.test').hide(3000)
    getTrending(1);
  }, []);

  return (
    <>
      <div className={`d-flex align-items-center ${style.bgHome}`}>
        <div className='text-white p-4'>
          <h1 className='fw-bolder'>{t('Home.trend film')}</h1>
          <p className='fw-bold'> {t('Home.about two')}</p>
        </div>
      </div>
      <HomeHeader />
      <section className='container'>
        <div className="row gy-3 align-items-stretch">
          <h1 className={`p-3 ${style.pageHeading}`}>{t('Home.trend film')}</h1>
          <div className="col-md-9 h-100">
            {trendingMovies ?
              <div className='row gy-2 align-items-stretch'>
                {trendingMovies.map((movie, i) => <div key={i} className='col-md-6'>
                  <div className='row align-items-stretch'>
                    <div className="col-md-6 mx-0 px-0">
                      <Link to={`/movie-details/${movie.id}`}>
                        <img className='w-100' src={'https://image.tmdb.org/t/p/w500' + movie.poster_path} alt="movie" />
                      </Link>
                    </div>
                    <div className={`col-md-6 mx-0 px-0 position-relative ${style.movieDark}`}>
                      <div className='p-3'>
                        <h2 className='h6 py-2 fw-bold'>{movie.title}</h2>
                        <p> {t('Home.release date')} :</p>
                        <p className='pb-5'> {movie.release_date}</p>
                        <div className='position-absolute bottom-0 pb-2'>
                          <Box
                            sx={{
                              width: 200,
                              display: 'flex',
                              alignItems: 'center',
                            }}
                          >
                            <Rating
                              name="text-feedback"
                              value={movie.vote_average}
                              readOnly
                              emptyIcon={<StarIcon style={{ opacity: 0.55 }} fontSize="inherit" />}
                            />
                            <Box className={`p-1 ${style.vote}`} sx={{ ml: 2 }}>{movie.vote_average}</Box>
                          </Box>
                          <Link to={`/movie-details/${movie.id}`}>
                            <i className={`p-1 fa-regular fa-eye ${style.eye}`}></i>
                          </Link>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>)}
              </div> :
              <div className='h-100 d-flex align-items-center justify-content-center spinner'>
                <i className='fas fa-spinner fa-spin fa-3x'></i>
              </div>
            }
            <div className='m-auto text-center my-3 d-flex justify-content-center w-100'>
              <nav aria-label="Page navigation example">
                <ul className="pagination">
                  {
                    nums.map((pageNum) =>
                      <li onClick={() => getTrending(pageNum)} key={pageNum} className={`page-item ${style.pagination}`}><a className="page-link">{pageNum}</a></li>)
                  }
                </ul>
              </nav>
            </div>
          </div>
          <div className="col-md-3 h-100">
            <img src={boot} className='w-100' alt="إعلان" />
            <img src={sale} className='w-100' alt="إعلان" />
          </div>
        </div>

        <div className="container m-auto text-center">
          <img src={boster} className='w-75' alt="إعلان" />
        </div>
      </section>
    </>
  )
}
