import React, { useContext } from 'react';
import avatar from '../../../../assets/user.png';
import { Link } from 'react-router-dom';
import style from './movies-style/Movies.module.css';
import { moviesContext } from '../../../shared/Context/Store';
import { useTranslation } from 'react-i18next';
import { Swiper, SwiperSlide } from 'swiper/react';
import 'swiper/css';
import 'swiper/css/effect-coverflow';
import 'swiper/css/pagination';
import { Pagination } from 'swiper/modules';

export default function Movies() {
    const { t } = useTranslation();
    let { trendingMovies, trendingTv, trendingPeople } = useContext(moviesContext)
    return (
        <>
            <div className={`d-flex align-items-center justify-content-center ${style.bgMovie}`}>
                <div>
                    <h1 className='fw-bolder text-white'>{t('Home.trend film')}</h1>
                </div>
            </div>
            <div className='container-fluid my-3' >

                <section className='row m-auto'>
                    <div className="col-md-3 d-flex align-items-center justify-content-center">
                        <div>
                            <h2 className='fw-bolder'>{t('movies.film')}</h2>
                            <p className='fw-bold py-3'> {t('Home.about two')}</p>
                            <hr />
                        </div>
                    </div>
                    <div className="col-md-9">
                        <div className='row'>
                            {trendingMovies.map((movie, i) => <div key={i} className='col-md-4 m-0 p-0'>
                                <div className={`${style.movie}`}>
                                    <div className={`${style.imgHover}`}>
                                        <Link to={`/movie-details/${movie.id}`}>
                                            <img className={`w-100${style.img}`} src={'https://image.tmdb.org/t/p/w500' + movie.poster_path} alt="movie" />
                                            <div className={`d-flex align-items-center justify-content-center  text-white p-3 ${style.protofolioImg}`}>
                                                <div className='text-center'>
                                                    <h5>{movie.title}</h5>
                                                    <i className={`p-1 fa-regular fa-eye`}></i>
                                                </div>
                                            </div>
                                        </Link>
                                    </div>
                                </div>
                            </div>)}
                        </div>
                    </div>
                </section>


                <div className='row my-3'>
                    <div className="d-flex mb-3">
                        <img src={require('../../../../assets/images/arrow-right.png')} className={`mx-2 ${style.arrow}`} alt="arrow" />
                        <h2 className={`pt-3 ${style.pageHeading}`}>{t('movies.tv')}</h2>
                    </div>
                    {trendingTv.map((tv, i) => <div key={i} className='col-md-2'>
                        <div className='movie'>
                            <img className='w-100' src={'https://image.tmdb.org/t/p/w500' + tv.poster_path} alt="tv" />
                            <h2 className='h6 py-2'>{tv.name}</h2>
                        </div>
                    </div>)}
                </div>

                <div className='row my-3'>
                    <div className="d-flex mb-3">
                        <img src={require('../../../../assets/images/arrow-right.png')} className={`mx-2 ${style.arrow}`} alt="arrow" />
                        <h2 className={`pt-3 ${style.pageHeading}`}>{t('movies.person')}</h2>
                    </div>
                    <Swiper
                        slidesPerView={6}
                        spaceBetween={0}
                        pagination={{
                            clickable: true,
                        }}
                        modules={[Pagination]}
                        className="mySwiper"
                    >
                        {trendingPeople.map((person, index) =>
                            <div className='col-md-2' key={index} >
                                <SwiperSlide key={index} >
                                    <div className={`${style.movie}`}>
                                        <div className={`${style.imgHover}`}>
                                            {person.profile_path === null ? <img src={avatar} alt="person" /> :
                                                <img className={`w-100 ${style.img}`} src={'https://image.tmdb.org/t/p/w500' + person.profile_path} alt="person" />}
                                            <div className={`d-flex align-items-center justify-content-center  text-white p-3 ${style.protofolioImg}`}>
                                                <div className='text-center'>
                                                    <h2 className='h6 py-2'>{person.name}</h2>
                                                    <h2 className='h6 py-2'>{person.known_for_department}</h2>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </SwiperSlide>
                            </div>)}
                    </Swiper>

                </div >
            </div >
        </>
    )
}
