import './App.css';
import React from 'react';
import Notfound from './app/shared/component/notfound';
import Header from './app/shared/component/Header';
import { Navigate, Route, Routes, useNavigate } from 'react-router-dom';
import Home from './app/project-roles/components/visitor/Home';
import Register from './app/project-roles/auth/Register';
import Login from './app/project-roles/auth/Login';
import { useEffect, useState } from 'react';
import jwtDecode from 'jwt-decode';
import Movies from './app/project-roles/components/user/Movies';
import MovieDetails from './app/project-roles/components/user/MovieDetails';
import { useTranslation } from "react-i18next";
import MoviesContextProvider from './app/shared/Context/Store';
import Footer from './app/shared/component/Footer/Footer';
import About from './app/project-roles/components/visitor/about/About';
import Contact from './app/project-roles/components/visitor/Contact/Contact';

function App() {
  const [t, i18n] = useTranslation('common');
  const { i18n: { language, dir } } = useTranslation();
  let navigate = useNavigate();
  const [userData, setUserData] = useState(null);
  function saveUserData() {
    let encodedToken = localStorage.getItem('userToken');
    let decodedToken = jwtDecode(encodedToken);
    setUserData(decodedToken);
  }

  useEffect(() => {
    if (localStorage.getItem('userToken')) {
      saveUserData();
    }
  }, []);

  function Logout() {
    setUserData(null);
    localStorage.removeItem('userToken');
    localStorage.removeItem('comments');
    navigate('./login');
  }

  function ProtectedRoute(props) {
    if (localStorage.getItem('userToken') === null) {
      return <Navigate to='/login' />
    } else {
      return props.children;
    }
  }

  return (
    <div style={{ direction: dir(language) }}>
      <MoviesContextProvider>
        <Header Logout={Logout} userData={userData} />
        <section>
          <Routes>
            <Route path='/' element={< Home />} />
            <Route path='home' element={<Home />} />
            <Route path='about' element={<About />} />
            <Route path='contact' element={<Contact />} />
            <Route path='movies' element={<ProtectedRoute><Movies /></ProtectedRoute>} />
            <Route path='movie-details' element={<ProtectedRoute><MovieDetails /></ProtectedRoute>}>
              <Route path=':id' element={<ProtectedRoute><MovieDetails /></ProtectedRoute>} />
            </Route>
            <Route path='register' element={< Register />} />
            <Route path='login' element={< Login saveUserData={saveUserData} />} />
            <Route path='*' element={<Notfound />} />
          </Routes>
        </section>
        <Footer />
      </MoviesContextProvider>

    </div>
  );
}

export default App;