import React from 'react'
import style from '../about/About.module.css';
import { useTranslation } from 'react-i18next';

export default function About() {
    const { t } = useTranslation();

    return (
        <div className='overflow-hidden'>
            <div>
                <div className='row gy-2'>
                    <div className="col-md-6 m-0 p-0">
                        <img src={require('../../../../../assets/images/pexels-photo-38196.webp')} className='w-100' alt="about" />
                    </div>
                    <div className="col-md-6 d-flex align-items-center justify-content-center">
                        <div className='p-3'>
                            <h5 className="fw-bolder pb-2">
                                {t('about.about us')}
                            </h5>
                            <p className="text-muted">
                                {t('Home.about')}
                            </p>
                        </div>
                    </div>
                    <div className="col-md-6 d-flex align-items-center justify-content-center">
                        <div className='p-3'>
                            <h5 className="fw-bolder pb-2">
                                {t('Home.trend film')}
                            </h5>
                            <p className="text-muted">
                                {t('Home.about three')}
                            </p>
                        </div>
                    </div>
                    <div className="col-md-6 m-0 p-0">
                        <img src="https://max-themes.net/demos/ztudiox/main/ztudiox/upload/pexels-photo-55789.jpg" className='w-100' alt="about" />
                    </div>
                </div>
            </div>
        </div>
    )
}
