import Axios from 'axios';
import React, { useEffect, useState } from 'react';
import { useParams } from 'react-router-dom';
import Avatar from '@mui/material/Avatar';
import AvatarGroup from '@mui/material/AvatarGroup';
import style from './movies-style/Movies.module.css';
import { useTranslation } from 'react-i18next';
import boot from '../../../../assets/images/boots.jpg';
import sale from '../../../../assets/images/sale.jpg';
import Rating from '@mui/material/Rating';
import Box from '@mui/material/Box';
import StarIcon from '@mui/icons-material/Star';
import styleHome from '../visitor/Home.module.css'
import { Swiper, SwiperSlide } from 'swiper/react';
import 'swiper/css';
import 'swiper/css/effect-coverflow';
import 'swiper/css/pagination';
import { Pagination } from 'swiper/modules';
import Grid from '@mui/material/Grid';
import Paper from '@mui/material/Paper';
import Typography from '@mui/material/Typography';
import ButtonBase from '@mui/material/ButtonBase';
import { styled } from '@mui/material/styles';
import avatar from '../../../../assets/user.png';
import Skeleton from '@mui/material/Skeleton';
import Stack from '@mui/material/Stack';

const Img = styled('img')({
    margin: 'auto',
    display: 'block',
    maxWidth: '100%',
    maxHeight: '100%',
});

export default function MovieDetails() {
    const [comment, setComments] = useState('')
    var siteNameInput = document.getElementById("comment");
    const { t } = useTranslation();
    var data = localStorage.getItem('comments');
    const [movieDetails, setMovieDetails] = useState(null);
    let params = useParams();
    async function getMovieDetails(id) {
        let { data } = await Axios.get(`https://api.themoviedb.org/3/movie/${id}?api_key=f1aca93e54807386df3f6972a5c33b50`);
        setMovieDetails(data);
    }

    function addComment() {
        setComments(siteNameInput.value)
        localStorage.setItem('comments', siteNameInput.value)
    }

    useEffect(() => {
        localStorage.getItem('comments')
        getMovieDetails(params.id);
        if (comment == '')
            return;
    }, [comment])

    return (
        <>
            <div className="container">

                {movieDetails ?
                    <div className='row m-auto my-3 gy-2'>
                        <h1 className={`p-3 ${style.pageHeading}`}>{movieDetails.title}</h1>
                        <div className="col-md-3">
                            <div className={`position-relative ${style.boster}`}>
                                <img className='w-100' src={'https://image.tmdb.org/t/p/w500' + movieDetails.poster_path} alt="movie" />
                                <div className={`${style.voteNumber}`}>
                                    <Box className={`p-1 ${styleHome.vote}`} sx={{ ml: 2 }}>{movieDetails.vote_average}</Box>
                                </div>
                                <div className='d-flex p-3'>
                                    <strong> {t('details.vote')} </strong> :
                                    <Box
                                        sx={{
                                            width: 200,
                                            display: 'flex',
                                            alignItems: 'center',
                                        }}
                                    >
                                        <Rating
                                            name="text-feedback"
                                            value={movieDetails.vote_average}
                                            readOnly
                                            emptyIcon={<StarIcon style={{ opacity: 0.55 }} fontSize="inherit" />}
                                        />
                                    </Box>
                                </div>
                            </div>
                        </div>
                        <div className="col-md-6">
                            <p className={`pb-3 fw-bold ${style.runTime}`} ><i className="fa-regular fa-clock"></i> <span>{movieDetails.runtime} min</span></p>
                            <p><strong>{t('Home.release date')} : </strong>{movieDetails.release_date}</p>
                            <p><strong>{t('details.revenu')} : </strong>{movieDetails.revenue}</p>
                            <p><strong>{t('details.budget')} : </strong> : {movieDetails.budget}</p>
                            <p><strong>{t('details.status')} : </strong> : {movieDetails.status}</p>
                            <div className='d-flex align-items-start w-100'>
                                <AvatarGroup className={`mt-2 ${style.avatarName}`} total={100}>
                                    <Avatar className={`${style.avatarName}`} alt="Remy Sharp" src="/static/images/avatar/1.jpg" />
                                    <Avatar className={`${style.avatarName}`} alt="Travis Howard" src="/static/images/avatar/2.jpg" />
                                    <Avatar className={`${style.avatarName}`} alt="Agnes Walker" src="/static/images/avatar/4.jpg" />
                                    <Avatar className={`${style.avatarName}`} alt="Trevor Henderson" src="/static/images/avatar/5.jpg" />
                                </AvatarGroup>
                            </div>
                        </div>
                        <div className="col-md-3">
                            <img src={boot} className='w-100' alt="إعلان" />
                            <img src={sale} className='w-100' alt="إعلان" />
                        </div>

                        <div className="col-md-9">
                            <h2 className={`pb-3 ${style.pageHeading}`}>{t('details.overview')}</h2>
                            <p> {movieDetails.overview}</p>

                            <div>
                                <Swiper
                                    slidesPerView={3}
                                    spaceBetween={30}

                                    pagination={{
                                        clickable: true,
                                    }}
                                    modules={[Pagination]}
                                    className="mySwiper"
                                >
                                    <SwiperSlide>
                                        <img src="https://image.tmdb.org/t/p/w500/jP2ik17jvKiV5sGEknMFbZv7WAe.jpg" />
                                    </SwiperSlide>
                                    <SwiperSlide>
                                        <img src="https://image.tmdb.org/t/p/w500/4m1Au3YkjqsxF8iwQy0fPYSxE0h.jpg" />
                                    </SwiperSlide>
                                    <SwiperSlide>
                                        <img src="https://image.tmdb.org/t/p/w500/qW4crfED8mpNDadSmMdi7ZDzhXF.jpg" />
                                    </SwiperSlide>
                                    <SwiperSlide>
                                        <img src="https://image.tmdb.org/t/p/w500/516MFML05KWiPfiQNAsVCGMySc7.jpg" />
                                    </SwiperSlide>
                                    <SwiperSlide>
                                        <img src="https://image.tmdb.org/t/p/w500/ehGIDAMaYy6Eg0o8ga0oqflDjqW.jpg" />
                                    </SwiperSlide>
                                    <SwiperSlide>
                                        <img src="https://image.tmdb.org/t/p/w500/ozC9UaKaS0MXjMbCAHZJikszwV6.jpg" />
                                    </SwiperSlide>
                                </Swiper>
                            </div>


                            <div className='my-4'>
                                <h2 className={`pb-3 ${style.pageHeading}`}>{t('details.comments')}</h2>
                                <textarea name="" id="comment" className='form-control mt-3' placeholder='add your comment'></textarea>

                                <div className='d-flex justify-content-end mt-2'>
                                    <button className={`px-2 btn ${style.btnADD}`} onClick={() => addComment()}> {t('details.add comment')} </button>
                                </div>

                                <Paper className='my-2'
                                    sx={{
                                        p: 2,
                                        maxWidth: 1000,
                                        flexGrow: 1,
                                    }}
                                >
                                    <Grid container spacing={2}>
                                        <Grid item>
                                            <ButtonBase sx={{ width: 128, height: 128 }}>
                                                <Img alt="complex" src={avatar} />
                                            </ButtonBase>
                                        </Grid>
                                        <Grid item xs={12} sm container>
                                            <Grid item xs container direction="column" spacing={2}>
                                                <Grid item xs>
                                                    <Typography gutterBottom variant="subtitle1" component="div">
                                                        Your Comment
                                                    </Typography>
                                                    <Typography variant="body2" color="text.secondary">
                                                        {new Date().toLocaleString() + ""}
                                                    </Typography>
                                                    <Typography variant="body2" gutterBottom>
                                                        {data}
                                                    </Typography>

                                                </Grid>
                                            </Grid>
                                        </Grid>
                                    </Grid>
                                </Paper>
                                <hr />
                                <Paper
                                    sx={{
                                        p: 2,
                                        maxWidth: 1000,
                                        flexGrow: 1,
                                    }}
                                >
                                    <Grid container spacing={2}>
                                        <Grid item>
                                            <ButtonBase sx={{ width: 128, height: 128 }}>
                                                <Img alt="complex" src={avatar} />
                                            </ButtonBase>
                                        </Grid>
                                        <Grid item xs={12} sm container>
                                            <Grid item xs container direction="column" spacing={2}>
                                                <Grid item xs>
                                                    <Typography gutterBottom variant="subtitle1" component="div">
                                                        Standard license
                                                    </Typography>
                                                    <Typography variant="body2" color="text.secondary">
                                                        today | 5 PM
                                                    </Typography>
                                                    <Typography variant="body2" gutterBottom>
                                                        dolor sit amet consectetur adipisicing elit. Aperiam, est.
                                                    </Typography>

                                                </Grid>
                                            </Grid>
                                        </Grid>
                                    </Grid>
                                </Paper>
                                <hr />
                                <Paper className='my-2'
                                    sx={{
                                        p: 2,
                                        maxWidth: 1000,
                                        flexGrow: 1,
                                    }}
                                >
                                    <Grid container spacing={2}>
                                        <Grid item>
                                            <ButtonBase sx={{ width: 128, height: 128 }}>
                                                <Img alt="complex" src={avatar} />
                                            </ButtonBase>
                                        </Grid>
                                        <Grid item xs={12} sm container>
                                            <Grid item xs container direction="column" spacing={2}>
                                                <Grid item xs>
                                                    <Typography gutterBottom variant="subtitle1" component="div">
                                                        Aya
                                                    </Typography>
                                                    <Typography variant="body2" color="text.secondary">
                                                        today |  12.5 PM
                                                    </Typography>
                                                    <Typography variant="body2" gutterBottom>
                                                        super, Lorem ipsum dolor sit amet.
                                                    </Typography>
                                                </Grid>
                                            </Grid>
                                        </Grid>
                                    </Grid>
                                </Paper>
                            </div>
                        </div>

                        <div className="col-md-3">

                            <div className={`${styleHome.boxSocial}`}>
                                <h2> {t('Home.movie app')}</h2>
                                <p className={`${styleHome.promoDescription}`}>{t('Home.smart phone')}</p>
                                <div>
                                    <img src="http://amovie.gozha.net/images/apple-store.svg" className="w-50" alt="apple" />
                                </div>
                                <div>
                                    <img src="http://amovie.gozha.net/images/components/wave-dark.svg" className={`${style.zigzag}`} alt="line" />
                                </div>
                                <div>
                                    <img src='http://amovie.gozha.net/images/google-play.svg' className="w-50" alt="google" />
                                </div>
                                <div>
                                    <img src="http://amovie.gozha.net/images/components/wave-dark.svg" className={`${style.zigzag}`} alt="line" />
                                </div>
                                <div>
                                    <img src="http://amovie.gozha.net/images/windows-store.svg" className="w-50" alt="windows" />
                                </div>
                            </div>
                        </div>
                    </div>
                    :
                    <Stack spacing={1}>
                        <div className='row mt-3'>
                            <div className="col-md-9">
                                <div className="d-flex flex-wrap">
                                    <div>
                                        <Skeleton variant="text" width={110} sx={{ fontSize: '1rem' }} />
                                        <Skeleton variant="rectangular" width={210} height={400} />
                                    </div>
                                    <div className='m-4'>
                                        <Skeleton variant="text" width={110} sx={{ fontSize: '1rem' }} />
                                        <Skeleton variant="text" width={110} sx={{ fontSize: '1rem' }} />
                                        <Skeleton variant="text" width={110} sx={{ fontSize: '1rem' }} />
                                        <Skeleton variant="text" width={110} sx={{ fontSize: '1rem' }} />
                                        <Skeleton variant="text" width={110} sx={{ fontSize: '1rem' }} />
                                        <Skeleton variant="text" width={110} sx={{ fontSize: '1rem' }} />
                                    </div>
                                </div>

                                <Skeleton variant="text" className='mt-3' width={270} sx={{ fontSize: '1rem' }} />

                                <div className="d-flex my-3">
                                    <Skeleton variant="rectangular" width={210} height={400} />
                                    <div className="mx-2">
                                        <Skeleton variant="rectangular" width={210} height={400} />
                                    </div>
                                    <Skeleton variant="rectangular" width={210} height={400} />
                                </div>
                            </div>
                            <div className="col-md-3">
                                <Skeleton variant="rounded" height={200} />
                                <div className='my-3'>
                                    <Skeleton variant="rounded" height={200} />
                                </div>
                                <Skeleton variant="rounded" height={200} />
                            </div>
                        </div>
                    </Stack>
                }
            </div>

        </>
    )
}
