import React from 'react'
import style from '../Footer/Footer.module.css'
import film from '../../../../assets/images/film-camera.png';
import { NavLink } from 'react-router-dom';
import { useTranslation } from 'react-i18next';

export default function Footer() {
    const { t } = useTranslation();

    return (
        <>
            <footer className={`d-flex justify-content-center align-items-center ${style.bgFooter}`}>
                <div className='container py-4 text-center'>
                    <img src={film} className={`${style.logo}`} alt="film" />

                    <p className='pt-3'>
                        BeTube
                    </p>

                    <div className='py-3'>

                        <NavLink className={`${style.iconHeader}`} to="home">{t('Home.Home')}</NavLink>
                        <NavLink className={`${style.iconHeader}`} to="about">{t('header.about')}</NavLink>
                        <NavLink className={`${style.iconHeader}`} to="projects">{t('header.contact')}</NavLink>

                    </div>
                </div>
            </footer>

            <section className={`d-flex justify-content-between align-items-center ${style.secondFooter}`}>
                <div className="container d-flex justify-content-between pt-2">
                    <p>© 2023 BeTube</p>
                    <div>
                        <i className="fa-brands fa-facebook-f socialLinks"></i>
                        <i className="fa-brands fa-twitter socialLinks"></i>
                        <i className="fa-brands fa-youtube socialLinks"></i>
                        <i className="fa-brands fa-google socialLinks"></i>
                    </div>
                </div>
            </section>
        </>
    )
}
