import React from 'react'
import { useTranslation } from 'react-i18next';
import style from '../Home.module.css'

export default function HomeHeader() {
    const { t } = useTranslation();

    return (
        <div className='my-2 container'>
            <div className="row gy-3 m-auto">
                <div className="col-md-3 d-flex justify-content-center align-items-center">
                    <p className="fw-bolder">
                        {t('Home.about')}
                    </p>
                </div>

                <div className="col-md-3 d-flex justify-content-center align-items-center">
                    <div>
                        <h3 className={`fw-bolder ${style.pageHeading}`}>{t('Home.join')}</h3>
                        <div>
                            <img src="http://amovie.gozha.net/images/components/wave-dark.svg" className={`${style.zigzag}`} alt="line" />
                        </div>
                        <p className='pt-4'>
                            {t('Home.about two')}
                        </p>
                        <p className='pt-2'>
                            {t('Home.about three')}
                        </p>
                    </div>
                </div>
                <div className="col-md-3 d-flex justify-content-center align-items-center">
                    <div>
                        <div className='position-relative'>
                            <img src="http://amovie.gozha.net/images/icons/location.svg" className='w-100' alt="location" />
                            <div className={`fw-bolder ${style.address}`}>
                                <p>
                                    {t('Home.watch')}
                                </p>
                            </div>
                        </div>

                        <div className='text-center m-auto pt-5'>
                            <i className={`fa-brands fa-facebook-f ${style.sochial}`}></i>
                            <i className={`fa-brands fa-twitter ${style.sochial}`}></i>
                            <i className={`fa-brands fa-youtube ${style.sochial}`}></i>
                            <i className={`fa-brands fa-google ${style.sochial}`}></i>
                            <i className={`fa-brands fa-instagram ${style.sochial}`}></i>
                            <i className={`fa-brands fa-vimeo-v ${style.sochial}`}></i>
                        </div>
                    </div>

                </div>
                <div className="col-md-3">
                    <div className={`${style.boxSocial}`}>
                        <h2> {t('Home.movie app')}</h2>
                        <p className={`${style.promoDescription}`}>{t('Home.smart phone')}</p>
                        <div>
                            <img src="http://amovie.gozha.net/images/apple-store.svg" className="w-50" alt="apple" />
                        </div>
                        <div>
                            <img src="http://amovie.gozha.net/images/components/wave-dark.svg" className={`${style.zigzag}`} alt="line" />
                        </div>
                        <div>
                            <img src='http://amovie.gozha.net/images/google-play.svg' className="w-50" alt="google" />
                        </div>
                        <div>
                            <img src="http://amovie.gozha.net/images/components/wave-dark.svg" className={`${style.zigzag}`} alt="line" />
                        </div>
                        <div>
                            <img src="http://amovie.gozha.net/images/windows-store.svg" className="w-50" alt="windows" />
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}
