import Axios from 'axios';
import Joi from 'joi';
import React, { useState } from 'react';
import { useNavigate, Link } from 'react-router-dom';
import { useTranslation } from 'react-i18next';
import style from './Auth.module.css';

export default function Register() {
  const [errorList, setErrorList] = useState([]);
  const [isLoading, setisLoading] = useState(false);
  const [error, setError] = useState('');
  let navigate = useNavigate();
  const { t } = useTranslation();

  const [user, setUser] = useState({
    first_name: '',
    last_name: '',
    age: 0,
    email: '',
    password: ''
  })

  function getUserData(e) {
    let myUser = { ...user };
    myUser[e.target.name] = e.target.value;
    setUser(myUser);
  }

  async function submitRegiserForm(e) {
    e.preventDefault();
    setisLoading(true);
    let validationResult = validateRegisterForm();
    if (validationResult.error) {
      setErrorList(validationResult.error.details);
      setisLoading(false);
    } else {
      let { data } = await Axios.post('https://movies-api.routemisr.com/signup', user);
      if (data.message === 'success') {
        setisLoading(false);
        navigate('/login')
      } else {
        setError(data.message);
        setisLoading(false);
      }
    }
  }

  function validateRegisterForm() {
    let scheme = Joi.object({
      first_name: Joi.string().alphanum().max(10).min(3).required(),
      last_name: Joi.string().alphanum().max(10).min(3).required(),
      age: Joi.number().min(16).required(),
      email: Joi.required(),
      password: Joi.required()
      // email: Joi.email({ minDomainSegments: 2, tlds: { allow: ['com', 'net'] } }).required(),
      // password: Joi.required().pattern(new RegExp('^[a-z][A-Z]{3,30}$')),
    });
    return scheme.validate(user, { abortEarly: false })
  }

  return (
    <>
      <div className='overflow-hidden'>
        <div className="row m-auto gy-2">
          <div className="col-md-4 d-flex justify-content-center align-items-center">
            <div>
              <h2 className='text-center py-2'>{t('auth.Register Now')}</h2>
              <form onSubmit={submitRegiserForm}>
                <div className="row gy-2">
                  <div className="col-md-6">
                    <label htmlFor="first_name">{t('auth.First Name')}</label>
                    <input onChange={getUserData} className='form-control mb-2' type="text" placeholder={`${t('auth.First Name')}`} id='first_name' name='first_name' />
                  </div>
                  <div className="col-md-6">
                    <label htmlFor="last_name">{t('auth.Last Name')}</label>
                    <input onChange={getUserData} className='form-control mb-2' type="text" placeholder={`${t('auth.Last Name')}`} id='last_name' name='last_name' />
                  </div>
                  <div className="col-md-6">
                    <label htmlFor="age">{t('auth.Age')}</label>
                    <input onChange={getUserData} className='form-control mb-2' type="number" placeholder={`${t('auth.Age')}`} id='age' name='age' />
                  </div>
                  <div className="col-md-6">
                    <label htmlFor="password">{t('auth.Password')}</label>
                    <input onChange={getUserData} className='form-control mb-2' type="password" placeholder={`${t('auth.Password')}`} id='password' name='password' />
                  </div>
                  <div className="col-md-12">
                    <label htmlFor="email">{t('contact.email')}</label>
                    <input onChange={getUserData} className='form-control mb-2' type="email" placeholder='example@gmail.com' id='email' name='email' />
                  </div>
                </div>
                <button onChange={getUserData} className='btn btn-secondary my-2 w-100'>
                  {isLoading === true ? <i className='fas fa-spinner fa-spin'></i> : `${t('auth.register')}`}
                </button>
                {errorList.map((error) => <div className='text-danger text-center py-1 my-2'>{error.message}</div>)}
                {error.length > 0 ? <div className='text-danger text-center py-1 my-2'>{error}</div> : ''}

                <hr />
                <p className="text-muted text-center">
                  <Link className="nav-link" to="/login">{t('auth.member')}</Link>
                </p>
              </form>
            </div>

          </div>
          <div className={`col-md-8 m-0 p-0 order-lg-last order-first ${style.bgLogin}`}>
            <div className={`d-flex justify-content-center align-items-center ${style.layout}`}>
              <h2 className='text-white'>{t('Home.trend film')}</h2>
            </div>
          </div>
        </div>
      </div>
    </>
  )
}
